<script>
  import {httpGet, httpPost} from '../../utils/util.js'
  import {mapState, mapMutations} from 'vuex'
  export default {
    data() {
      return {
        navRadio:0,
        iRadio: 'CNY',
        oRadio: 'CNY',
        lRadio:0,
        coins: [],
        inNum: '',
        outNum: '',
        inwalletAddress: [],
        outwalletAddress: [],

        tranderList: [],
        trander: '',
        tranderInfo: [],
        ads1: 0,
        ads2: 0,

        iCurren: '',
        oCurrencys: [{
          oCurrency: '选项1',
          label: 'bitepie'
        }, {
          oCurrency: '选项2',
          label: '支付宝'
        }, {
          oCurrency: '选项3',
          label: '微信'
        }, {
          oCurrency: '选项4',
          label: '银行'
        }, {
          oCurrency: '选项5',
          label: '比特'
        }],
        oCurren: '',

        severFee: '',
        guarFee: '',

        addressListShow1:false,
        addressListShow2:false,
        addTrFlag: false,
        t_name: '',
        t_address_in: '',
        t_address_out: '',
        texttip: '',
        modalFlag: false,
        cSuccessFlag: false
      }
    },
    computed: {
      ...mapState(["userInfo"])
    },
    mounted() {
      this.getSymbols();
      this.getAddress(this.iRadio,this.inwalletAddress);
      this.getAddress(this.oRadio,this.outwalletAddress);

      this.TraderObject();
      this.TraderInfo();
    },
    methods: {
      //获取货币种类
      getSymbols() {
        var that = this;
        httpGet("/symbols/symbols",{},function (res) {
          if(res.data.error_code == 0) {
            that.coins = res.data.symbols;
          }
        })
      },
      //查找交易员的钱包获取入账、出账地址
      getAddress(v,address) {
        var that = this;
        httpGet("/trader_wallets/search",{
          trader_id: that.userInfo.trader_id,
          symbol: v,
          sid: that.userInfo.sid
        },function (res) {
          if(res.data.error_code == 0) {
            var wallets = res.data.trader_wallet;
            for(var key in wallets){
              var item = {};
              item.value = wallets[key].id;
              item.label = wallets[key].wallet_name;
              address.push(item);
            }
          }
        })
      },
      //获取交易者列表
      TraderObject() {
        var that = this;
        httpGet("/traders/profile",{
          type: 1,
          sid: that.userInfo.sid
        },function (res) {
          if (res.data.error_code == 0) {
            var traders = res.data.trader;
            var obj = {};
            var arr = [];
            for(var key in traders){
              var item = {};
              item.value = traders[key].id;
              item.label = traders[key].name;
              arr.push(item);
            }
            obj.label = "内部人员";
            obj.options = arr;
            that.tranderList.push(obj);
          }
        });
        httpGet("/traders/profile",{
          type: 2,
          sid: that.userInfo.sid
        },function (res) {
          if (res.data.error_code == 0) {
            var traders = res.data.trader;
            var obj = {};
            var arr = [];
            for(var key in traders){
              var item = {};
              item.value = traders[key].id;
              item.label = traders[key].name;
              arr.push(item);
            }
            obj.label = "外部人员";
            obj.options = arr;
            that.tranderList.push(obj);
          }
        });
      },
    //获取交易者信息
      TraderInfo() {
        var that = this;
        httpGet("/trader_wallets/search",{
          trader_id: this.trander,
          sid: that.userInfo.sid
        },function (res) {
          var datas = res.data.trader_wallet;
          var item = {};
          var item2 = [];
          item.name = datas[0].trader_name;
          item.trader_id = datas[0].trader_id;
          that.tranderInfo.push(item);
          for (var key in datas) {
            item2.push( datas[key].wallet_name);
          }
          that.tranderInfo.push(item2);
        })
      },
    //提交信息，生成记账账单
      confirBtn() {
        var that = this;
        httpPost("/orders/create",{
          type: that.navRadio,
          detail: that.lRadio,
          input_symbol: that.iRadio,
          input_amount: that.inNum,

          input_wallet_id: that.iCurren,

          output_symbol: that.oRadio,
          output_amount: that.outNum,

          output_wallet_id: that.oCurren,

          output_fee: that.severFee,
          output_esscow_fee: that.guarFee,
          remark: that.texttip,
          other_id: that.trander,
          other_input_wallet_id: that.tranderInfo[1][that.ads1],
          other_output_wallet_id: that.tranderInfo[1][that.ads2],
          other_name: that.t_name,
          other_input_address: that.t_address_in,
          other_output_address: that.t_address_out,
          sid: that.userInfo.sid
        },function (res) {
          if(res.data.error_code == 0) {
            that.modalFlag = false;
            that.cSuccessFlag = true;
            setTimeout(function () {
              that.cSuccessFlag = false;
              that.$router.push('/home');
            },1000);
          }else {
            alert(res.data.error_reason);
          }
        })
      }
    }
  }
</script>
