    line-height: 36px;
    border-radius: 4px;
    background-color: #00C6FF;
    text-align: center;
    color: #fff;
    cursor: pointer;
  }
  .itemLeft{
    width: 120px;
  }
  .active{
    color: #00C6FF;
  }
  .t_item input{
    width: 260px;
    height: 35px;
    margin-top: -15px;
    border: 0;
    outline: none;
    border-bottom: 1px solid #333;
  }
</style>




